.navigation{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    /* justify-content:space-between;
    align-items: center; */
    background-color:rgb(22, 138, 121);
    padding: 5px 53px;
    /* height:70px; */
    justify-content:space-between;
    align-items: center;

    /* below 4 lines can use to make navbar fix at its position even after scrolling */
    top: 0;
    /* z-index: 100; */
    position: fixed;
    width: 100%;
    flex: 2 1 auto;
}


.navigation>div>ul{
    list-style:  none;
}
.navigation>div>ul>li{
   font-size: 20px;
   color: white;
}
.navBarInputBox{
    margin: 10px 17px 10px 20px;
    font-size: 15px;
    padding: 11px 17px;
    border-radius: 30px;
    border: 1px solid rgb(98 221 203);
    width: 498px;
    outline: none;
    background-color: white;
}